<template>
  <div>
    <div class="content">
      <div class="content_item1">
        <el-menu
          :default-active="$store.getters.topNavState"
          class="el-menu-demo"
          mode="horizontal"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          router
        >
          <el-menu-item v-for="(item, index) in topNav" :key="index" :index="item.path">
            <svg-icon :icon-class="item.icon"></svg-icon>
            {{ $t('router.'+item.name) }}
          </el-menu-item>
        </el-menu>
      </div>
      <div class="content_item2" @click="fullScreen">
        <svg-icon style="font-size: 25px;height: 60px;" icon-class="全屏"></svg-icon>
      </div>
      <div class="content_item2">
        <div class="logOut">
          <el-dropdown  trigger="click" @command="changeLanguage">
            <span class="el-dropdown-link">
              {{language}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="zh">{{$t('language.zh')}}</el-dropdown-item>
              <el-dropdown-item command="en">{{$t('language.en')}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
      <div class="content_item2">
        <div class="logOut">
          <el-dropdown placement="bottom" @command="handleCommand" trigger="click">
            <span class="el-dropdown-link">
              <i class="el-icon-user-solid"></i>
              {{ userInfo.name}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="userInfo">个人信息</el-dropdown-item>
              <el-dropdown-item command="forgetPassword">忘记密码</el-dropdown-item>
              <el-dropdown-item command="logOut">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="topNav_breadcrumb">
      <Breadcrumb />
    </div>
  </div>
</template>

<script>
import Breadcrumb from '../components/breadcrumb'
import {mapGetters} from 'vuex'
export default {
  components:{
    Breadcrumb
  },
  data() {
    return {
      topNav: [],
      isfullScreen:true,
      language: this.$t('language.zh')
    };
  },
  computed: {
     ...mapGetters(['userInfo'])
   },
  watch: {
    $route: function(to, from) {
      // 路由改变时执行
      //console.info("to.path:" + to.path);
      this.fetchNavData();
    }
  },
  methods: {
    getTopNav() {
      let routers = this.$router.options.routes;
      this.topNav = routers.filter((item, idx) => {
        if (item.children) {
          return item;
        }
      });
    },
    fetchNavData() {
      // 初始化菜单激活项
      let cur_path = this.$route.path, //获取当前路由
        routers = this.$router.options.routes, // 获取路由对象
        topNavState = "";
      routers.forEach((element, index) => {
        if (element.path == cur_path) {
          topNavState = element.path;
        } else {
          let children = element.children;
          if (children) {
            children.forEach((item, idx) => {
              if (item.path == cur_path) {
                topNavState = element.path;
                // leftNavState = item.path;
              }
            });
          }
        }
      });

      this.$store.commit("setTopNavState", topNavState);
    },
    // 个人信息
    handleCommand(command) {
      switch (command) {
        case 'userInfo':
          console.log('个人信息')
          break;
        case 'forgetPassword':
          console.log('个人信息')
          break;
        default:
          this.$confirm("确定要退出吗?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.$message.success('退出成功')
              localStorage.removeItem("user");
              localStorage.removeItem("role");
              localStorage.removeItem("token");
              // this.$router.push("/login");
              window.location.href = "/login"
            })
            .catch(() => {
              // this.$message({
              //   type: "info",
              //   message: "已取消"
              // });
            });
          break;
      }
    },
    // 改变语言
    changeLanguage(command) {
      switch (command) {
        case 'zh':
          this.language = this.$t('language.zh')
          this.$i18n.locale = 'zh'
          break;
        case 'en':
          this.language = this.$t('language.en')
          this.$i18n.locale = 'en'
          break;
        default:
          this.language = this.$t('language.zh')
          this.$i18n.locale = 'zh'
          break;
      }
    },
    fullScreen () {
      if (this.isfullScreen) {
        var docElm = document.documentElement
        if (docElm.requestFullscreen) {
          docElm.requestFullscreen()
        } else if (docElm.mozRequestFullScreen) {
          docElm.mozRequestFullScreen()
        } else if (docElm.webkitRequestFullScreen) {
          docElm.webkitRequestFullScreen()
        } else if (elem.msRequestFullscreen) {
          elem.msRequestFullscreen()
        }
        this.isfullScreen = false
      } else {
        if (document.exitFullscreen) {
          document.exitFullscreen()
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen()
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen()
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen()
        }
        this.isfullScreen = true
      }
    },

  },
  created() {
    this.getTopNav();
    this.fetchNavData();
  }
};
</script>
<style lang="less" scoped>
.content {
  height: 60px;
  line-height: 60px;
  width: 100%;
  background: #545c64;
  display: flex;
}
.content_item1{
  flex-grow: 1;
}
.content_item2{
  padding:0 15px;
}
.logOut {
  cursor: pointer;
  i {
    color: #fff;
  }
  span {
    color: #fff;
  }
}
.topNav_breadcrumb{
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
</style>



