import router from '../../router/index'
import {
  publicRoutes,
  roleRoutes
} from '../../router/index'
const app = {
  state: {
    //防止刷新vuex值清空
    userInfo: JSON.parse(localStorage.getItem(`user`)),
    role: JSON.parse(localStorage.getItem(`role`)),
    token: JSON.parse(localStorage.getItem(`token`)),
    menuList: []
  },
  mutations: {
    //个人信息
    SET_USER: (state, data) => {
      state.userInfo = data
      localStorage.setItem("user", JSON.stringify(data));
    },
    //设置菜单
    SET_MENU(state, role) {
      if (role == null) {
        return false
      }
      localStorage.setItem("role", JSON.stringify(role));
      let list = [...roleRoutes]
      // 递归删除没权限的路由，或者根据有权限的push到list得到一个新数组
      function eachSelect(routers, userRole) {
        for (let j = 0; j < routers.length; j++) {
          if (routers[j].meta && routers[j].meta.role && routers[j].meta.role.indexOf(userRole) === -1) {
            routers.splice(j, 1)
            j = j != 0 ? j - 1 : j
          }
          if (routers[j].children && routers[j].children.length) {
            eachSelect(routers[j].children, userRole)
          }
        }
      }
      eachSelect(list, role)
      state.menuList = [...publicRoutes, ...list]
      router.options.routes = [...publicRoutes, ...list];
      router.addRoutes(list)
    },
    // 设置token
    SET_TOKEN(state, data) {
      state.token = data
      localStorage.setItem("token", JSON.stringify(data));
    },
  },
  actions: {

  }
}

export default app



